.freebies-content p{
    @apply font-default text-[14px] font-normal leading-[28px] tracking-[0.14px];
}

.freebies-scene-gallery img.scene-item.scene-show.scene-active {
    max-height: 5000px;
    opacity: 1;
    overflow: unset!important;
    &:not(:first-child){
        @apply pt-6;
    }
}

.popup-submit-form.show-popup, .popup-result-submit.show-popup {
    @apply h-screen opacity-100 z-[100] transition-opacity duration-1000 fixed;
}

.popup-submit-form.hidden-popup, .popup-result-submit.hidden-popup {
    @apply h-0 opacity-0 overflow-hidden z-0 transition-colors duration-[5000ms];
}
